import React from "react";

const Text = (props) =>{
    const klasse = props.rules.skema[0]
    const {action} = props
    return <div>
        <p className="text-light">Kathrine quickly finds her spot in the class. She can't help but yawn quite a bit as she prepares herself for the first class of the day. Maths. <br/> This really isn't what she would like to be doing right now, but before she knows it her teacher comes marching into the room. {klasse.teacherChange ? "She": "He"} looks over the class with that wry smile that {klasse.teacherChange ? "she": "he"} usually has at this time in the morning. {klasse.teacher} clears {klasse.teacherChange ? "her": "his"} throat before continuing. "As you all know, we are gonna have a quiz today. It is gonna be a Kahoot... get it?" {klasse.teacherChange ? "She": "He"} seems awfully proud of the joke that {klasse.teacherChange ? "she": "he"} wasn't even responsible for, but Kathrine can't help but cringe at it.  </p>
        <img className="img-fluid rounded" alt="Classroom" src={require("../../resources/class.jpg")}/>
        <p className="text-light">The smartboard powers up and soon the iconic music is blaring over the speakers while questions appear on the screen.</p>
        <div className="bg-secondary">
            <h5 className="text-light text-center text-weight-bold">The square of a negative number is also called:</h5>
            <div className="form-check">
                <input onClick={(e)=>action({quizChoice:e.target.value})} className="form-check-input" type="radio" name="kahootQuiz" id="whole" value={false}/>
                <label className="form-check-label text-light" htmlFor="whole">
                    A whole number
                </label>
            </div>
            <div className="form-check">
                <input onClick={(e)=>action({quizChoice:e.target.value})} className="form-check-input" type="radio" name="kahootQuiz" id="int" value={false}/>
                <label className="form-check-label text-light" htmlFor="int">
                    An integer
                </label>
            </div>
            <div className="form-check">
                <input onClick={(e)=>action({quizChoice:e.target.value})} className="form-check-input" type="radio" name="kahootQuiz" id="img" value={true}/>
                <label className="form-check-label text-light" htmlFor="img">
                    An imaginary number
                </label>
            </div>
            <div className="form-check">
                <input onClick={(e)=>action({quizChoice:e.target.value})} className="form-check-input" type="radio" name="kahootQuiz" id="dec" value={false}/>
                <label className="form-check-label text-light" htmlFor="dec">
                    A decimal number
                </label>
            </div>
        </div> 
    </div>
}
export default Text;