const initState = {
    Time:0,
    Title:"Start",
}

export const timeReducer = (state = initState, action) =>{
    switch(action.type){
        case "RESET_TIME":
            state = {...state, Time: 0}
            return state
        case "INCREASE_TIME":
            state = {...state, Time: state.Time + 1}
            return state
        case "END":
            state={...state, Time: -2}
        case "RESET_ALL":
            return initState
        default:
            return state
    }
}