import React from "react";
import {GetText} from "../functions/getText";
import {connect} from "react-redux";

class Main extends React.Component{
    constructor(props){
        super(props)
        this.state={
            end: false
        }
    }

    action = (object) =>{
        this.props.setItem(object)
    }

    setEnd = () =>{
        if(!this.state.end){
            this.setState({...this.state,end:true})
        }
    }

    Progress = (e) =>{
        e.preventDefault();
        if(this.state.end){
            this.props.endLine()
            this.setState({...this.state,end:false})
        }else{
            this.props.increaseTime()
        }
        
    }

    render(){
        const {meta,choices} = this.props;
        return(
            <div className="bg-dark">
                <h1 className="text-light text-center">{meta.Title}</h1>
                <div className="col-6 offset-3">
                    <GetText end={this.setEnd} bool={this.state.bool} rules={choices} action={(object)=>this.action(object)} time={meta.Time}/>
                    <button className="btn btn-block sticky-bottom btn-light" onClick={this.Progress}>Progress</button>
                </div>
            </div>
        )
    }
        
}

const MapStateToProps = (state) =>{
    console.log(state)
    return {
        meta: state.timeReducer,
        choices: state.choiceReducer
    }
}

const MapDispatchToProps = (dispatch) =>{
    return{
        increaseTime: (payload) => {dispatch({type:"INCREASE_TIME"})},
        setItem: (payload) => {dispatch({type:"CHANGE_ITEMS", payload:payload})},
        endLine: () => {dispatch({type:"END"})}
    }
}
export default connect(MapStateToProps,MapDispatchToProps)(Main)