import React from 'react';
import './App.css';
import {connect} from "react-redux";

class App extends React.Component{
  render(){
    return(
      <nav className="navbar navbar-dark bg-dark sidebar">
        <button className="btn btn-light" onClick={()=>this.props.reset()}>Reset</button>
      </nav>
    )
  }
}

const mapStateToProps = (state) =>{
  return{
    meta: state.timeReducer,
    choices: state.choiceReducer
  }
}

const mapDispatchToProps = (dispatch) =>{
  return{
    reset: (payload) => {dispatch({type:"RESET_ALL"})}
  }
}

export default connect(mapStateToProps,mapDispatchToProps)(App);
