import React from "react";
import Text1 from "./text/1FirstRealText";
import Text2 from "./text/2SecondIntro";
import Text3 from "./text/3Firstchoice";
import Text4 from "./text/4Breakfast";
import Text5 from "./text/5Bustrip";
import Text6 from "./text/6SchoolLanding";
import Text7 from "./text/7Scheduel";
import Text8 from "./text/8Quiz";
import Text9 from "./text/9Aftermath";
import Text10 from "./text/10";
import Text11 from "./text/11";
import Text12 from "./text/12";
import Text13 from "./text/13";
import Text14 from "./text/14";
import Text15 from "./text/15";
import Text16 from "./text/16";
import Text17 from "./text/17";
import Text18 from "./text/18";
import Text19 from "./text/19";
import Text20 from "./text/20";


export const GetText = (props) =>{
    let {rules, time, action, end} = props
    switch(time){
        case 0: 
            return <div> 
                <p className="text-light">Welcome to this experiment. While trying to learn to code I decided to put some my new-found knowledge into a project. The following project will probably have many shortcomings compared to projects created with things like twine, but never the less it functions more as a learning experience than anything else.<br/>Please be adviced that the following page contains items of adult nature and by clicking Progress you express an indirect degree of understanding of this.</p>
            </div>
        case 1:
                let item = rules.start;
            return( 
                <div>
                    <p className="text-light"> Through out the game you will see words that are bold and can be clicked. <br/> <span className="Hand font-weight-bolder text-danger text-center" onClick={()=>action({start:"CHANGE!"})}>{item}</span> <br/> Some words might change permanently. While others can be toggled back and forth. <br/><span className="Hand font-weight-bolder text-danger text-center" onClick={()=>action({ bool:!rules.bool})}>{rules.bool ? "On" : <span className="animated 1 fadeIn slow">Off</span>}</span><br/>You can change as many things on a page as you like, however every time you press progress, the time of the story will move forward.</p>
                </div>)
        case 2:
            return (
                <Text1 rules={rules} action={action}/>
            )
        case 3:
            return(
                <Text2 rules={rules} action={action}/>
            )
        case 4: 
            return(
                <Text3 rules={rules} action={action} end={end}/>
            )
        case 5:
            return(
                <Text4 rules={rules} action={action}/>
            )
        case 6:
            return(
                <Text5 rules={rules} action={action}/>
            )
        case 7:
            return(
                <Text6 rules={rules} action={action}/>
            )
        case 8:
            return(
                <Text7 rules={rules} action={action}/>
            )
        case 9:
            return(
                <Text8 rules={rules} action={action}/>
            )
        case 10:
            return(
                <Text9 rules={rules} action={action} />
            )
        /*case 11:
            return(
                <Text10 rules={rules} action={action}/>
            )
        case 12:
            return(
                <Text11 rules={rules} action={action}/>
            )
        case 13:
            return(
                <Text12 rules={rules} action={action}/>
            )
        case 14:
            return(
                <Text13 rules={rules} action={action}/>
            )
        case 15:
            return(
                <Text14 rules={rules} action={action}/>
            )
        case 16:
            return(
                <Text15 rules={rules} action={action}/>
            )
        case 17:
            return(
                <Text16 rules={rules} action={action}/>
            )
        case 18:
            return(
                <Text17 rules={rules} action={action}/>
            )
        case 19:
            return(
                <Text18 rules={rules} action={action}/>
            )
        case 20:
            return(
                <Text19 rules={rules} action={action}/>
            )
        case 21:
            return(
                <Text20 rules={rules} action={action}/>
            )*/
        default:
            return <div> 
                <p className="text-light">That is all for now folks. Again this is a learning experience. If you have something you wanna share then feel free to leave a comment wherever you found this game.</p>
            </div>
    }   
}